<h2 id="question-1-1p" tabindex="-1"><a class="header-anchor" href="#question-1-1p" aria-hidden="true">#</a> Question 1 (1p)</h2>
<ExamQuestion>
<template v-slot:question>
<p>Name the method one should use in HTTP when the request is about:</p>
<ul>
<li>Retrieving a resource</li>
<li>Creating a new resource</li>
<li>Deleting a resource</li>
</ul>
<p><strong>Note</strong>: This question is about HTTP, and not about websites.</p>
<p>You will get:</p>
<ul>
<li>0.33 points for each correct name</li>
</ul>
</template>
<template v-slot:sample-answer>
<ul>
<li>Retrieving a resource: <code>GET</code></li>
<li>Creating a new resource: <code>POST</code></li>
<li>Deleting a resource: <code>DELETE</code></li>
</ul>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>0.33 points for each correct name</li>
</ul>
</template>
</ExamQuestion>
<h2 id="question-2-1p" tabindex="-1"><a class="header-anchor" href="#question-2-1p" aria-hidden="true">#</a> Question 2 (1p)</h2>
<ExamQuestion>
<template v-slot:question>
<p>Specify the status code that should be sent back in HTTP when:</p>
<ul>
<li>The requested resource doesn't exist</li>
<li>The server encountered an unknown problem and therefor can't carry out the request</li>
<li>The requested resource exists and is being sent back in the body of the response</li>
</ul>
<p>You will get:</p>
<ul>
<li>0.33 points for each correct status code</li>
</ul>
</template>
<template v-slot:sample-answer>
<ul>
<li>The requested resource doesn't exist: <code>404</code></li>
<li>The server encountered an unknown problem and therefor can't carry out the request: <code>500</code></li>
<li>The requested resource exists and is being sent back in the body of the response: <code>200</code></li>
</ul>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>0.33 points for each correct status code</li>
</ul>
</template>
</ExamQuestion>
<h2 id="question-3-1p" tabindex="-1"><a class="header-anchor" href="#question-3-1p" aria-hidden="true">#</a> Question 3 (1p)</h2>
<ExamQuestion>
<template v-slot:question>
<p>Name 2 different HTTP headers, explain what they mean/what they represent, explain if they can be used in requests or responses (or both), and give example of a value they can have.</p>
</template>
<template v-slot:sample-answer>
<p><code>Content-Length</code> can be used in both HTTP requests and HTTP responses, and its value describes how many bytes the body consists of, for example <code>20</code>.</p>
<p><code>Content-Type</code> can be used in both HTTP requests and HTTP responses, and its value describes in which data format the body is written, for example <code>text/html</code>.</p>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>0.125 points for each correct name</li>
<li>0.125 points for each correct explanation of what they represent</li>
<li>0.125 points for each correct can be used in requests/responses</li>
<li>0.125 points for each correct sample value</li>
</ul>
</template>
</ExamQuestion>
<h2 id="question-4-2p" tabindex="-1"><a class="header-anchor" href="#question-4-2p" aria-hidden="true">#</a> Question 4 (2p)</h2>
<ExamQuestion>
<template v-slot:question>
<p>In HTTP, clients can pass data to the server both in the querystring (part of the URI) and in the body of the HTTP request. Explain when they should use which one.</p>
</template>
<template v-slot:sample-answer>
<p>Since the querystring is part of the URI, and the URI is used to identify the resource(s) the request is about, the data should be passed in the querystring only when it's used to identify the resource(s) the request is about, such as when searching.</p>
<p>The data should be passed in the body of the request when the data is not used to identify which resource(s) the request is about, but represent some additional information that is part of a request the server needs to carry out the request, for example when the data represents a new resource that should be created on the server.</p>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>2 points for correct explanation</li>
<li>1 point for saying something like <code>GET</code> = retrieve data = URI, <code>POST</code> = send data = body (this is how we often use it, but the question is general and not for these two specific methods)</li>
<li>-0.5 points for claiming that body of request is more secure than querystring without justifying why</li>
</ul>
</template>
</ExamQuestion>
