<h2 id="question-8-1p" tabindex="-1"><a class="header-anchor" href="#question-8-1p" aria-hidden="true">#</a> Question 8 (1p)</h2>
<ExamQuestion>
<template v-slot:question>
<p>On a webpage, explain why it's better to use a <code>&lt;form&gt;</code> with a submit button to send an HTTP request to delete a resource instead of using a <code>&lt;a&gt;</code>.</p>
</template>
<template v-slot:sample-answer>
<p>One can only send GET requests with <code>&lt;a&gt;</code>, and those requests shouldn't have any side-effects on the server. Therefor it's better to use <code>&lt;form&gt;</code> instead, through which one can send a POST request, that may have side-effects on the server.</p>
<p>(optimally one should send a DELETE requests, but one can't do that with any HTML element)</p>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>0.33 points for one should send a POST request</li>
<li>0.33 points for <code>&lt;a&gt;</code> can only send GET requests</li>
<li>0.33 points for <code>&lt;form&gt;</code> can send POST requests</li>
<li>-0.1 points for claiming strange/untrue/irrelevant things</li>
</ul>
<p>OR:</p>
<ul>
<li>1 point for saying anti-forgery tokens can be used in <code>&lt;form&gt;</code> (those can be used in <code>&lt;a&gt;</code> too (e.g. in the query string), so not really a valid justification, but OK)</li>
</ul>
</template>
</ExamQuestion>
<h2 id="question-9-1p" tabindex="-1"><a class="header-anchor" href="#question-9-1p" aria-hidden="true">#</a> Question 9 (1p)</h2>
<ExamQuestion>
<template v-slot:question>
<p>On a webpage, to display the <code>&amp;</code> character you should write it as it's character entity instead, i.e. <code>&amp;amp;</code>. Why?</p>
</template>
<template v-slot:sample-answer>
<p>The <code>&amp;</code> character has special meaning in HTML: to start a character entity (to express a character in a special way, like when you want the web browser to display <code>&lt;</code> instead of interpreting it as the start of a tag, you write is as <code>&amp;lt;</code>). If you really want the web browser to display <code>&amp;</code>, you need to express <code>&amp;</code> as its character entity (<code>&amp;amp;</code>), otherwise the web browser would read <code>&amp;</code> as the start of a character entity.</p>
<div class="custom-container warning"><p class="custom-container-title">Answer the question</p>
<p>Many answers say something like <em>One can escape HTML code by using <code>&amp;lt;</code>, and similar</em>, but that doesn't answer the question why one can't write <code>&amp;</code> to display the <code>&amp;</code> character.</p>
</div>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>1 point for correct answer</li>
</ul>
</template>
</ExamQuestion>
<h2 id="question-10-1p" tabindex="-1"><a class="header-anchor" href="#question-10-1p" aria-hidden="true">#</a> Question 10 (1p)</h2>
<ExamQuestion>
<template v-slot:question>
<p>If you want a web browser to display a list of items like this:</p>
<ul>
<li>List Item 1</li>
<li>List Item 2</li>
<li>List Item 3</li>
<li>List Item ...</li>
</ul>
<p>You can use the following HTML code:</p>
<div class="language-html ext-html line-numbers-mode"><pre v-pre class="language-html"><code><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>p</span><span class="token punctuation">></span></span> • List Item 1<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>p</span><span class="token punctuation">></span></span>
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>p</span><span class="token punctuation">></span></span> • List Item 2<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>p</span><span class="token punctuation">></span></span>
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>p</span><span class="token punctuation">></span></span> • List Item 3<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>p</span><span class="token punctuation">></span></span>
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>p</span><span class="token punctuation">></span></span> • List Item ...<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>p</span><span class="token punctuation">></span></span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br></div></div><p>Why is this not a good way to do it?</p>
</template>
<template v-slot:sample-answer>
<p>When the web browser renders the HTML code, it will look like a list to a human, but other tools reading our HTML code, such as web spiders/search engines, will not get the understanding that this is a list, but will instead read it as different paragraphs. By using HTML properly and mark this text as a list using <code>&lt;ul&gt;</code> and <code>&lt;li&gt;</code> instead of <code>&lt;p&gt;</code>, we improve the semantic, and these other tools will get a better understanding of our text.</p>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>1 point for not using markup/semantic</li>
</ul>
<div class="custom-container warning"><p class="custom-container-title">Answer the question</p>
<p>Just saying it's more correct/better to use <code>&lt;ul&gt;</code> and <code>&lt;li&gt;</code> is not enough to get any points. The question was not <em>How should one do it?</em>, but <em>Why is it bad to use <code>&lt;p&gt;</code> this way?</em></p>
<p>Feel free to mention that <code>&lt;ul&gt;</code> and <code>&lt;li&gt;</code> are better to use, but then you need to explain why they are better to use (markup/semantic).</p>
</div>
</template>
</ExamQuestion>
