<h2 id="question-14-1p" tabindex="-1"><a class="header-anchor" href="#question-14-1p" aria-hidden="true">#</a> Question 14 (1p)</h2>
<ExamQuestion>
<template v-slot:question>
<p>Explain what the following terms are:</p>
<ul>
<li>Encoding</li>
<li>Encryption</li>
<li>Hashing</li>
</ul>
<p>Especially explain how they differ.</p>
</template>
<template v-slot:sample-answer>
<p><strong>Encoding</strong> <br>
A way to express values as other values, for example map characters to numbers, such as <code>A</code> to <code>65</code>, <code>B</code> to <code>66</code>, etc. Can be used to represent characters in a computer. No secrets here.</p>
<p><strong>Encryption</strong> <br>
A way to encrypt a message (to make a text unreadable to others). The one doing the encryption usually has one key for the encryption algorithm used to generated the encrypted message, and the one that wants to be able to read the encrypted message needs one key (could be the same as the one used for encrypting the message) to do the decryption and obtain the original message again. For encryption to be useful, at least one of the keys needs to kept secret (not available to everybody).</p>
<p><strong>Hashing</strong> <br>
A way to make a message unreadable to others and that can't be reversed (the original message can't be obtained again, unless a brute-force technique is used (usually very slow and impractical to use)). Often used for storing passwords (store the hash value of the password) or to compute a short &quot;fingerprint&quot; of a message.</p>
</template>
<template v-slot:marking-guidelines>
<div class="custom-container warning"><p class="custom-container-title">It's not random!</p>
<p>Very many students answer something like <em>It generates a random string</em>, or <em>It consists of random characters</em>. That is not true, all three are very much deterministic. You can write things like <em>The hash value seems to consist of random characters</em>, and similar, but nothing is random in the algorithms (except generation of keys/salt).</p>
</div>
<ul>
<li>0.33 points for encoding mapping characters to numbers (no secrets)</li>
<li>0.33 points for encryption makes messages unreadable but can then be decrypted using a decryption key</li>
<li>0.33 points for hashing makes messages unreadable and can't (easily) be made readable again</li>
<li>-0.1 points for saying that encrypted messages/hash values are random</li>
</ul>
</template>
</ExamQuestion>
<h2 id="question-15-1p" tabindex="-1"><a class="header-anchor" href="#question-15-1p" aria-hidden="true">#</a> Question 15 (1p)</h2>
<ExamQuestion>
<template v-slot:question>
<p>When hashing, explain what <em>salting</em> is, and when and why you need to use that.</p>
</template>
<template v-slot:sample-answer>
<p><em>Salting</em> is a way of storing hash values of passwords more secure. A hash algorithm should always produce the same output for the same input. Hackers can therefor generate a <em>rainbow table</em> that contains simple, short and well-known passwords and their respective hash value. If a hacker then comes across the hash values of our users passwords, they can easily use their rainbow table to figure out which password our users have that uses one of these simple, short and well-known passwords.</p>
<p>So, instead of simply hashing the passwords our users have, we append a string of our choice to the users password, known as the <em>salt</em>, and hashes <code>the password + the salt</code>. So even if our users use simple, short and well-known passwords, the hash value won't exist in the hacker's rainbow table, and therefor our users passwords will be stored in a little bit more secure way.</p>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>0.33 points for explaining what salting is (extra string added to passwords before hashing)</li>
<li>0.33 points for explaining when we need to use salt (when storing passwords)</li>
<li>0.33 points for explaining why we need use it (to protect against existing rainbow tables/harder to reverse)</li>
</ul>
</template>
</ExamQuestion>
<h2 id="question-16-2p" tabindex="-1"><a class="header-anchor" href="#question-16-2p" aria-hidden="true">#</a> Question 16 (2p)</h2>
<ExamQuestion>
<template v-slot:question>
<p>Carl needs a web application to which he can send a single HTTP request, and when the web application receives the HTTP request, it should do something (for example remove all data stored in the web application, so the web application basically restarts). Carl don't want anyone to be to send that HTTP request, so he will send a password along with the request, and the web application should only carry out the request if the correct password is provided.</p>
<p>When sending passwords, it's of course important to use HTTPS. But Carl thinks he has a workaround to this. He thinks that if he uses client-side JavaScript to hash his password directly in the web browser, and only send the hash value of the password to the server, he doesn't need to use HTTPS, but can stick to using HTTP.</p>
<p>Has Carl come up with a good workaround or not? Justify your answer.</p>
</template>
<template v-slot:sample-answer>
<p>No, Carl has not come up with a good solution. The problem is that any man-in-the-middle (anyone that helps forwarding the HTTP request to the server) can see the hash value of his password. This basically means that any man-in-the-middle can send a request to the server with the same hash value and that way restart the web application (Carl's password would still remain unknown to them though).</p>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>2 points for correct answer and valid justification, such as:
<ul>
<li>Man-in-the-middle doesn't need to know Carl's password, she can re-send the request with the same hash value whenever she want to restart Carl's web application</li>
<li>Man-in-the-middle can see carl's hash value and brute-force (e.g. use rainbow tables) Carl's password and login to other websites where Carl uses the same password</li>
<li>Mai-in-the-middle can send back client-side JavaScript (instead of the web app's client-side JavaScript) that sends the password Carl enters to the hacker's website, and then the hacker can login to Carl's accounts that use the same password on other websites</li>
</ul>
</li>
</ul>
<div class="custom-container warning"><p class="custom-container-title">About the justification</p>
<p>Just saying something like <em>HTTP is not secure, one must always use HTTPS</em> is not enough to get any points; one must justify the answer by describing how a hacker would take advantage of the bad solution to get any points.</p>
</div>
</template>
</ExamQuestion>
