<h2 id="question-19-3p" tabindex="-1"><a class="header-anchor" href="#question-19-3p" aria-hidden="true">#</a> Question 19 (3p)</h2>
<ExamQuestion>
<template v-slot:question>
<p>Users can create and login to accounts on Website A, and thereafter write blogposts belonging to their own accounts. After logging in it's also possible to delete your own account (and your blogposts) by sending a GET request to <code>/accounts/delete-me</code>.</p>
<p>Website A also contains a Cross-site Request Forgery vulnerability. Imagine you are a hacker and would like to delete the account for a specific user. Explain how you would do that (mention details).</p>
</template>
<template v-slot:sample-answer>
<p>One way to do that is by:</p>
<ol>
<li>Wait until the user posts a new blogpost on Website A. This likely means the user has an active login session (since no user clicks on the logout button when they are done).</li>
<li>Send an email to the user with an image that will be loaded from <code>website-a.com/accounts/delete-me</code> (<code>website-a.com</code> = domain name for Website A).</li>
<li>If the user opens the email in the same web browser that contains the active login session and the web browser displays images in emails by default (or the user agrees to display the image), then the web browser will send an HTTP GET request for the image with a valid login session, so the web application will basically delete the user's account when it receives that HTTP request.</li>
</ol>
</template>
<template v-slot:marking-guidelines>
<ul>
<li>3 points for answers that work.</li>
</ul>
<p>OR:</p>
<ul>
<li>2 points for answers that describes the general procedure but lack information on how to get the specific targeted user to send the unintentional request.</li>
</ul>
</template>
</ExamQuestion>
