<h2 id="question-21-2p" tabindex="-1"><a class="header-anchor" href="#question-21-2p" aria-hidden="true">#</a> Question 21 (2p)</h2>
<ExamQuestion>
<template v-slot:question>
<p>In JavaScript, a book with a title, number of pages and average number of words per page can be represented as an object like this:</p>
