<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br></div></div><p>Assume a reader reads 2 words per second. A book is considered to be <em>long</em> if it takes more than 8 hours to read. Implement the function <code>getLongBooks(books)</code>, which receives an array of book objects, and returns a new array containing all the <em>long</em> books.</p>
</template>
<template v-slot:sample-answer>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">const</span> <span class="token constant">SECONDS_PER_MINUTE</span> <span class="token operator">=</span> <span class="token number">60</span>
<span class="token keyword">const</span> <span class="token constant">MINUTES_PER_HOUR</span> <span class="token operator">=</span> <span class="token number">60</span>
<span class="token keyword">const</span> <span class="token constant">READ_WORDS_PER_SECOND</span> <span class="token operator">=</span> <span class="token number">2</span>
<span class="token keyword">const</span> <span class="token constant">LONG_BOOK_MIN_HOURS</span> <span class="token operator">=</span> <span class="token number">8</span>
<span class="token keyword">const</span> <span class="token constant">LONG_BOOK_MIN_SECONDS</span> <span class="token operator">=</span> <span class="token constant">LONG_BOOK_MIN_HOURS</span> <span class="token operator">*</span> <span class="token constant">MINUTES_PER_HOUR</span> <span class="token operator">*</span> <span class="token constant">SECONDS_PER_MINUTE</span>

<span class="token keyword">function</span> <span class="token function">getNumberOfWordsInBook</span><span class="token punctuation">(</span><span class="token parameter">book</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    <span class="token keyword">return</span> book<span class="token punctuation">.</span>numberOfPages <span class="token operator">*</span> book<span class="token punctuation">.</span>averageNumberOfWordsPerPage
<span class="token punctuation">}</span>

<span class="token keyword">function</span> <span class="token function">getSecondsToReadBook</span><span class="token punctuation">(</span><span class="token parameter">book</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    <span class="token keyword">return</span> <span class="token function">getNumberOfWordsInBook</span><span class="token punctuation">(</span>book<span class="token punctuation">)</span> <span class="token operator">/</span> <span class="token constant">READ_WORDS_PER_SECOND</span>
<span class="token punctuation">}</span>

<span class="token keyword">function</span> <span class="token function">isBookLong</span><span class="token punctuation">(</span><span class="token parameter">book</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    <span class="token keyword">return</span> <span class="token constant">LONG_BOOK_MIN_SECONDS</span> <span class="token operator">&lt;=</span> <span class="token function">getSecondsToReadBook</span><span class="token punctuation">(</span>book<span class="token punctuation">)</span>
<span class="token punctuation">}</span>

<span class="token keyword">function</span> <span class="token function">getLongBooks</span><span class="token punctuation">(</span><span class="token parameter">books</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    <span class="token keyword">const</span> longBooks <span class="token operator">=</span> <span class="token punctuation">[</span><span class="token punctuation">]</span>
    <span class="token keyword">for</span><span class="token punctuation">(</span><span class="token keyword">const</span> book <span class="token keyword">of</span> books<span class="token punctuation">)</span><span class="token punctuation">{</span>
        <span class="token keyword">if</span><span class="token punctuation">(</span><span class="token function">isBookLong</span><span class="token punctuation">(</span>book<span class="token punctuation">)</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
            longBooks<span class="token punctuation">.</span><span class="token function">push</span><span class="token punctuation">(</span>book<span class="token punctuation">)</span>
        <span class="token punctuation">}</span>
    <span class="token punctuation">}</span>
    <span class="token keyword">return</span> longBooks
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br><span class="line-number">20</span><br><span class="line-number">21</span><br><span class="line-number">22</span><br><span class="line-number">23</span><br><span class="line-number">24</span><br><span class="line-number">25</span><br><span class="line-number">26</span><br><span class="line-number">27</span><br></div></div></template>
